.cat-info {
  display: flex;
  margin-top: 15px;
}

.breed_image {
  width: 100%;
  height: auto;
  max-width: 450px;
}

.breed-details {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  
}


.loader {
    width: 48px;
    height: 48px;
    border: 5px solid #000;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    position: relative;
    animation: pulse 1s linear infinite;
  }
  .loader:after {
    content: '';
    position: absolute;
    width: 48px;
    height: 48px;
    border: 5px solid #000;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: scaleUp 1s linear infinite;
  }
  
  @keyframes scaleUp {
    0% { transform: translate(-50%, -50%) scale(0) }
    60% , 100% { transform: translate(-50%, -50%)  scale(1)}
  }
  @keyframes pulse {
    0% , 60% , 100%{ transform:  scale(1) }
    80% { transform:  scale(1.2)}
  }